            [{ align: [] }], // 
            ["clean"], // 
            ["image", "video"], // 
          ],
        },
      },
    };
  },
  computed: {
    // 
    editor() {
      return this.$refs.myQuillEditor.quill;
    },
  },
  mounted() {
    const content = ""; // 
    this.str = this.escapeStringHTML(content);
  },
  methods: {
    onEditorReady() {
      // 
    },
    onEditorBlur() {}, // 
    onEditorFocus() {}, // 
    onEditorChange() {
      this.$emit("change", this.escapeStringHTML(this.content));
    }, // 
    // 
    escapeStringHTML(str) {
      str = str.replace(/&lt;/g, "<");
      str = str.replace(/&gt;/g, ">");
      return str;
    },
  },
};
