<template>
  <div class="article-section">
    <div class="article-hd">
      <router-link to="/admin/article/create">  </router-link>
      <div class="article-search">
        <el-input v-model="search" size="40px" placeholder="">
          <el-button slot="append" icon="el-icon-search" @click="articleFilter">
            
          </el-button>
        </el-input>
      </div>
    </div>
    <div class="artilce-list">
      <el-table
        :data="tableData"
        border
        height="80vh"
        :default-sort="{ prop: 'id', order: 'ascending' }"
      >
        <el-table-column prop="index" fixed sortable label="index" width="100">
        </el-table-column>
        <el-table-column
          prop="title"
          label=""
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="120"
          :filters="classifyData"
          :filter-method="filterHandler"
        >
        </el-table-column>
        <el-table-column
          prop="created_at"
          label=""
          width="180"
          sortable
        >
        </el-table-column>
        <el-table-column
          prop="updated_at"
          label=""
          width="180"
          sortable
        >
        </el-table-column>
        <el-table-column prop="visits" label="" width="120" sortable>
        </el-table-column>
        <el-table-column fixed="right" label="" width="200">
          <template slot-scope="scope">
            <el-button
              @click="handleToEdit(scope.row.id)"
              type="text"
              size="small"
              icon="el-icon-edit"
              style="margin-right: 20px"
              ></el-button
            >
            <el-button
              type="text"
              size="small"
              icon="el-icon-delete"
              @click="deleteRow(scope.row.id)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import articleModel from "../../models/article";

export default {
  data() {
    return {
      select: "",
      search: "",
      tableData: [],
      name: [],
      classifyData: [],
    };
  },
  created() {
    this.article();
  },
  watch: {
    search() {
      this.article();
    },
  },
  methods: {
    article() {
      articleModel.article().then((res) => {
        this.tableData = res;
        this.tableData.forEach((data) => {
          data.created_at = this.$moment(data.created_at).format(
            "YYYY-MM-DD HH:mm:ss"
          );
          if (data.updated_at) {
            data.updated_at = this.$moment(data.updated_at).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          }
        });
        this.classifyFilter();
      });
    },
    classifyFilter() {
      const tableData = [...this.tableData];
      for (let i = 0; i < tableData.length; i++) {
        if (this.name.indexOf(tableData[i].name) > -1) {
          continue;
        }
        this.name.push(tableData[i].name);
        this.classifyData.push({
          text: tableData[i].name,
          value: tableData[i].name,
        });
      }
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    articleFilter() {
      const val = this.search;
      const data = this.tableData.filter((data) => {
        let dataStr = "";
        for (let i in data) {
          dataStr = dataStr + data[i] + ",";
        }
        return dataStr.includes(val);
      });
      this.tableData = data;
    },
    handleToEdit(id) {
      this.$router.push({ path: `/admin/article/:${id}/edit` });
    },
    deleteRow(id) {
      const msg = confirm("");
      if (msg) {
        articleModel.deleteArticle(id);
        this.article();
      }
    },
  },
};
</script>

<style lang="less" scoped>
.article-hd {
  font-size: 30px;
}
.article-search {
  width: 400px;
  float: right;
}
.artilce-list {
  margin-top: 30px;
}
/deep/.el-table__column-filter-trigger {
  margin-left: 30px;
}
</style>
